@use "./fonts" as fnt;

$size-width: 100px;
$size-height: 100vh;
$padding-100: 100px;
$padding-50: 50px;
$margin-100: 100px;
$width-100percent: 100%;

.saijero-page-landing {
  .RGBA-lighting {
    animation: RGBA 5s alternate infinite;
  }

  .wp-main-wrap {
    position: relative;

    .default-cursor {
      position: absolute;
      width: 50px;
      height: 50px;
      background: var(--url-cursor-pointer-blue);
      background-repeat: no-repeat;
      background-size: cover;
      z-index: 2;
      transition: transform 0.5s;
      transform: scale(0.7);
      pointer-events: none;
      box-shadow: 0 0 0 black;
    }

    .new-cursor {
      position: absolute;
      width: 50px;
      height: 50px;
      background: var(--url-cursor-pointer-blue-select);
      background-repeat: no-repeat;
      background-size: cover;
      z-index: 2;
      transition: transform 0.5s;
      transform: scale(0.7);
      pointer-events: none;
      margin-left: -5px;
      margin-top: 1px;
    }
  }

  .transition {
    background: var(--url-landing-background);
    background-color: var(--11CBD7);
    background-position: 0 -175px;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    transition: ease 2s;
    height: 100%;

    &::before {
      content: "";
      width: 100%;
      height: 100%;
      display: block;
      position: fixed;
      background: var(--00000050);
    }
  }

  .home-background {
    background-size: cover !important;
    transition: 0.9s ease-out;
    background-position: center !important;
    background-repeat: no-repeat !important;
    background-attachment: fixed !important;
    background: var(--url-landing-background);
    background-color: var(--FF0000D0) !important;

    &::before {
      content: "";
      width: 100%;
      height: 100%;
      display: block;
      position: fixed;
      background: var(--00000050);
    }
  }
}

#wp-saijero-details {
  .wp-main-wrap {
    display: grid;
    position: relative;

    .wp-navigation-wrap {
      top: 0;
      left: 0;
      z-index: 99;
      position: sticky;
      display: inline-flex;

      .wp-wrap-side-bar {
        position: absolute;
        flex-direction: row;
        display: inline-flex;

        .wp-sub-wrap {
          top: 0;
          left: 0;
          z-index: 99;
          display: flex;
          padding: 0 10px;
          overflow: hidden;
          position: relative;
          align-items: center;
          height: $size-height;
          width: $size-width * 3;
          flex-direction: column;
          backdrop-filter: blur(3px);
          background: var(--00000033);
          padding-top: $padding-50 - 15;

          .wp-saijero-logo {
            width: 100%;
            height: 135px;
            border-radius: 15px;
            align-items: center;
            display: inline-grid;
            grid-auto-flow: column;
            border: 1px solid var(--368164);
            grid-template-columns: repeat(2, 1fr);
            background: linear-gradient(271deg, black, #8d535345);

            &::before {
              content: "";
              width: 5px;
              height: 95px;
              background: red;
              position: absolute;
              transform: translateX(140px);
            }

            a {
              margin: auto;
              border-radius: 50%;

              img {
                aspect-ratio: 1/1;
                border-radius: 50%;
                width: 100% !important;
                background: var(--FF0000);
              }
            }

            .wp-visited {
              gap: 5px;
              margin: auto;
              align-items: center;
              display: inline-flex;
              flex-direction: column;
              justify-content: center;

              .visited {
                font-weight: 700;
                color: var(--FFFFFF);
                display: inline-flex;
                font-family: fnt.$Croissant-family;
                text-shadow: 0 0 0px var(--FF00006B);

                &:nth-child(1) {
                  font-size: 15px;
                  letter-spacing: 1.5px;
                }

                &:nth-child(2) {
                  font-size: 15.5px;
                  color: var(--FF0000);
                  text-transform: uppercase;
                }
              }
            }

            a {
              overflow: hidden;
              width: $size-width;
              align-items: center;
              display: inline-flex;
              justify-content: center;

              img {
                width: $size-width - 15px;
              }
            }
          }

          .wp-nav-bar {
            width: $width-100percent;
            align-items: center;
            display: inline-flex;
            margin-top: $margin-100;

            .wp-list-item {
              width: $width-100percent;
              flex-direction: column;
              justify-content: center;

              .wp-list-link {
                overflow: hidden;
                align-items: center;
                display: inline-flex;
                justify-content: center;

                a {
                  font-family: fnt.$Pridi-family;
                }

                &:last-child {
                  bottom: -250px;
                  position: relative;

                  a {
                    border-radius: 0 90px;
                  }
                }

                .active {
                  color: var(--B0B61B);
                  transition: 1.5s ease-out;
                  background: var(--64CCC56B);
                  border-radius: 90px 0px 90px 0px !important;
                }

                a {
                  gap: 10px;
                  font-size: 18px;
                  font-weight: 500;
                  padding: 15px 69px;
                  border-radius: 20px;
                  align-items: center;
                  color: var(--FFE600);
                  display: inline-flex;
                  width: $width-100percent;
                  transition: 0.3s ease-out;
                  justify-content: flex-end;
                  text-transform: uppercase;
                  flex-direction: row-reverse;
                  background: var(--FF00006B);
                  border: 1px solid var(--1597BB);
                  text-shadow: 0 0 5px var(--160708);

                  &:hover {
                    transition: 0.8s ease;
                    color: var(--5EFF00);
                    border: 1px solid var(--B92444);
                    background: var(--176B878E) !important;
                  }

                  svg {
                    font-size: 35px;
                    color: var(--00FFDA);
                  }
                }
              }
            }
          }
        }

        .wp-show-bar {
          width: 45px;
          z-index: 100;
          margin: auto;
          overflow: hidden;
          position: relative;
          transition: 2s ease;
          height: $size-height;
          display: inline-block;

          .wp-button-wrap {
            height: 100%;
            margin: auto;
            display: inline-flex;
            transform: translateX(-30px);

            .wp-button-bar {
              width: 100%;
              margin: auto;
              text-align: center;
              padding: 10px 25px;
              color: var(--E4C988);
              transform: rotate(90deg);
              background: var(--176B87C0);
              border-radius: 20px 20px 0px 0px;

              &:hover {
                color: var(--FF0000);
                background: var(--4A74A4);
                transition: 1s ease;
              }
            }

            .active {
              transition: 2s ease;
            }
          }
        }
      }
    }

    .wp-toggle-page {
      z-index: 1;
      position: relative;
    }
  }
}

@keyframes RGBA_Shadow {
  0% {
    background-color: rgb(255, 0, 0);
    box-shadow: 0px 0px 35px rgb(255, 0, 0);
  }
  15% {
    box-shadow: 0px 0px 35px rgb(255, 238, 0);
    background-color: rgb(255, 238, 0);
    animation-duration: 4s; /* Fast speed */
  }
  30% {
    background-color: rgb(0, 255, 34);
    box-shadow: 0px 0px 35px rgb(0, 255, 34);
  }
  45% {
    box-shadow: 0px 0px 35px rgb(0, 238, 255);
    background-color: rgb(0, 238, 255);
    animation-duration: 6s; /* Fast speed */
  }
  55% {
    box-shadow: 0px 0px 35px rgb(4, 0, 255);
    background-color: rgb(4, 0, 255);
  }
  75% {
    box-shadow: 0px 0px 35px rgb(255, 0, 242);
    background-color: rgb(255, 0, 242);
    animation-duration: 2s; /* Fast speed */
  }
  100% {
    box-shadow: 0px 0px 35px rgb(255, 0, 0);
    background-color: rgb(255, 0, 0);
    animation-duration: 1s; /* Fast speed */
  }
}

@keyframes box_shadow {
  0% {
    box-shadow: 0px 0px 35px rgb(255, 0, 0);
  }
  15% {
    box-shadow: 0px 0px 35px rgb(255, 238, 0);
    animation-duration: 4s; /* Fast speed */
  }
  30% {
    box-shadow: 0px 0px 35px rgb(0, 255, 34);
  }
  45% {
    box-shadow: 0px 0px 35px rgb(0, 238, 255);
    animation-duration: 6s; /* Fast speed */
  }
  55% {
    box-shadow: 0px 0px 35px rgb(4, 0, 255);
  }
  75% {
    box-shadow: 0px 0px 35px rgb(255, 0, 242);
    animation-duration: 2s; /* Fast speed */
  }
  100% {
    box-shadow: 0px 0px 35px rgb(255, 0, 0);
    animation-duration: 1s; /* Fast speed */
  }
}
@keyframes box_shadow_down_20 {
  0% {
    box-shadow: 0px 0px 50px rgb(255, 0, 0);
  }
  15% {
    box-shadow: 0px 0px 50px rgb(255, 238, 0);
    animation-duration: 4s; /* Fast speed */
  }
  30% {
    box-shadow: 0px 0px 50px rgb(0, 255, 34);
  }
  45% {
    box-shadow: 0px 0px 50px rgb(0, 238, 255);
    animation-duration: 6s; /* Fast speed */
  }
  55% {
    box-shadow: 0px 0px 50px rgb(4, 0, 255);
  }
  75% {
    box-shadow: 0px 0px 50px rgb(255, 0, 242);
    animation-duration: 2s; /* Fast speed */
  }
  100% {
    box-shadow: 0px 0px 50px rgb(255, 0, 0);
    animation-duration: 1s; /* Fast speed */
  }
}

@keyframes RotationReact {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes RotationJs {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}

@keyframes Rotate_arrow {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}

@keyframes RGBA {
  0% {
    background-color: rgb(255, 0, 0);
  }
  15% {
    background-color: rgb(255, 238, 0);
    animation-duration: 4s; /* Fast speed */
  }
  30% {
    background-color: rgb(0, 255, 34);
  }
  45% {
    background-color: rgb(0, 238, 255);
    animation-duration: 6s; /* Fast speed */
  }
  55% {
    background-color: rgb(4, 0, 255);
  }
  75% {
    background-color: rgb(255, 0, 242);
    animation-duration: 2s; /* Fast speed */
  }
  100% {
    background-color: rgb(255, 0, 0);
    animation-duration: 1s; /* Fast speed */
  }
}

@keyframes shadow {
  0% {
    text-shadow: 0px 0px 0px var(--FF0000);
  }
  20% {
    text-shadow: 0px 0px 0px var(--5EFF00);
    animation-duration: 4s; /* Fast speed */
  }
  40% {
    text-shadow: 0px 0px 0px var(--00FFF2);
  }
  60% {
    text-shadow: 0px 0px 0px var(--003CFF);
    animation-duration: 6s; /* Fast speed */
  }
  80% {
    text-shadow: 0px 0px 0px var(--FF00FF);
  }
  100% {
    text-shadow: 0px 0px 0px var(--B92444);
    animation-duration: 2s; /* Fast speed */
  }
}

@keyframes boxshadow {
  0% {
    box-shadow: 0px 0px 35px rgb(255, 0, 0, 0.229);
  }
  15% {
    box-shadow: 0px 0px 35px rgb(255, 238, 0, 0.229);
    animation-duration: 4s; /* Fast speed */
  }
  30% {
    box-shadow: 0px 0px 35px rgb(0, 255, 34, 0.229);
  }
  45% {
    box-shadow: 0px 0px 35px rgb(0, 238, 255, 0.229);
    animation-duration: 6s; /* Fast speed */
  }
  55% {
    box-shadow: 0px 0px 35px rgb(0, 17, 255, 0.229);
  }
  75% {
    box-shadow: 0px 0px 35px rgb(255, 0, 242, 0.229);
    animation-duration: 2s; /* Fast speed */
  }
  100% {
    box-shadow: 0px 0px 35px rgb(255, 0, 0, 0.229);
    animation-duration: 1s; /* Fast speed */
  }
}

@keyframes transition_travel_1 {
  0%,
  20% {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
  }
  50% {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    scale: 1.5;
    position: absolute;
    animation-direction: reverse;
  }
  100% {
    top: -75%;
    left: -40%;
    right: 0;
    bottom: 0;
    scale: 0.9;
    position: absolute;
  }
}

@keyframes transition_travel_2 {
  0%,
  20% {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
  }
  50% {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    scale: 1.5;
    perspective: 900px;
    position: absolute;
  }
  100% {
    top: -115%;
    left: -40%;
    right: 0;
    bottom: 0;
    scale: 0.9;
    position: absolute;
  }
}
