@use "../../assets/scss/global/fonts" as fnt;

.saijero-page-landing {
  .wp-animate-call {
    z-index: -1;
    top: 100px;
    left: 350px;
    width: 100%;
    height: 100%;
    max-width: 1000px;
    position: absolute;
    display: inline-block;

    #self-development {
      .wp-parag-wrap {
        display: inline-grid;

        .wp-title-content {
          letter-spacing: 5px;
          margin-bottom: 5px;
          font-family: fnt.$Stardos-family;
        }

        .wp-support-content {
          letter-spacing: 2px;
        }

        .wp-content-wrap {
          margin-top: 1rem;
          margin-bottom: 2rem;

          .wp-parag-content {
            font-size: 20px;
            margin-left: 2rem;
          }
        }

        .wp-map-location {
          .wp-sub-wrap {
            margin-top: 10px;

            .wp-wrap-map {
              width: 100vw;
              height: 60vh;
              margin-top: 25px;
              display: inline-grid;

              iframe {
                width: 65%;
                height: 100%;
              }
            }
          }
        }
      }
    }
  }
}
