@use "../../assets/scss/global/fonts" as fnt;

$font_size: 5.5rem;
$margin_5rem_left: 5rem;

.saijero-page-landing {
  #wp-home-custom-content {
    button {
      z-index: 999;
      position: absolute;
      display: block;
    }

    width: 100% !important;
    background: linear-gradient(115deg, var(--00000050), var(--00000060));

    &::before {
      z-index: 1;
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: var(--00000033);
    }

    .wp-ban-content {
      width: 100%;
      grid-area: auto;
      align-items: center;
      display: inline-flex;
      justify-content: center;

      .wp-ban-text-welcome {
        gap: 5px;
        width: 100%;
        max-width: 900px;
        margin-right: 45px;
        align-items: center;
        display: inline-flex;
        flex-direction: column;
        justify-content: center;

        .wp-wrap-parag {
          width: 100%;
          margin: -5px 0;
          display: inline-block;

          .wp-text-name {
            font-size: 30px;
            font-weight: 700;
            letter-spacing: 3px;
            color: var(--FF8D29);
            font-family: fnt.$Stardos-family;
          }

          .wp-span {
            font-size: 24px;
            font-weight: 700;
            color: var(--00FFD1);
            text-transform: capitalize;
          }

          .wp-highlight-text {
            font-size: 24px;
            font-weight: 700;
            color: var(--5DFF00);
          }

          .wp-text-parag {
            font-size: 20px;
            font-weight: 600;
            letter-spacing: 2.5px;
            color: var(--FFFFFF);
          }

          &:nth-child(1) {
            .wp-text-parag {
              font-size: 35px;
              padding-left: 15px;
              margin-left: -25px;
              border-radius: 5px;
              font-family: fnt.$Stardos-family;
              border-left: 10px double var(--FF8D29);
            }
          }

          &:nth-child(2) {
            .wp-text-parag {
              .wp-text-name {
                margin-left: 5px;
              }
            }
          }

          &:last-child {
            .wp-text-parag {
              margin-top: 5px;
              line-height: 1.9;
            }
          }
        }
      }

      .wp-sub-wrap-ban {
        height: 100vh;
        display: inline-flex;
        align-items: flex-end;
        flex-direction: column;
        justify-content: center;
        background-position: center;
        background-repeat: no-repeat;

        .wp-saijero-img {
          width: 450px;
          padding-top: 25px;
          aspect-ratio: 0.76/1;
          display: inline-block;
          transform: rotateY(180deg);
          background: var(--00FFD160);
          overflow-y: visible !important;
          border-radius: 25px 25px 50% 50%;
          border: 10px double var(--00FFE0);
          box-shadow: 0 0 35px var(--8FD6E1);
          animation: RGBA_Shadow 10s linear infinite;
        }

        .wp-text-back {
          width: 445px;
          font-size: 55px;
          font-weight: 700;
          margin-top: -25px;
          position: absolute;
          letter-spacing: 3px;
          display: inline-flex;
          color: var(--00FFF2);
          margin-bottom: -250px;
          text-transform: uppercase;
          justify-content: space-around;
          text-shadow: 0 0 25px var(--000000);
          font-family: fnt.$Tilt_Prism-family;
        }
      }
    }

    // end here
    .wp-ban-svg {
      padding: 4rem 0;
      flex-direction: row;
      align-items: center;
      border-radius: 25px;
      display: inline-flex;
      justify-content: center;
      backdrop-filter: sepia(1);
      background: var(--1A59758A);

      .wp-wrap-sub {
        gap: 35px;
        flex-direction: row;
        align-items: center;
        display: inline-grid;
        grid-auto-flow: column;

        .wp-svg {
          gap: 15px;
          align-items: center;
          display: inline-flex;
          flex-direction: column;
          justify-content: center;

          &:nth-child(1n + 0) {
            svg {
              animation: RotationReact 12s alternate infinite;
            }
          }

          &:nth-child(2n + 0) {
            svg {
              animation: RotationJs 12s alternate infinite;
            }
          }

          svg {
            color: var(--16E279);
            font-size: $font_size;
            transition: transform 0.2s, font-size 0.2s;

            &:hover {
              transform: scale(5);
            }
          }

          .wp-name {
            z-index: 99;
            font-size: 25px;
            font-weight: 700;
            text-align: center;
            color: var(--FFFFFF);
          }
        }
      }
    }

    // end here
    .wp-recent-self-project {
      width: 100%;
      margin-top: 3rem;
      border-radius: 2rem;
      background: var(--9DB2BF2C);

      .wp-wrap-content {
        width: 80%;
        margin: auto;
        padding-bottom: 5rem;

        .wp-home-run {
          font-size: 25px;
          margin-top: 8rem;
          letter-spacing: 2px;
          color: var(--1597BB);
          text-transform: uppercase;
        }

        .wp-text-parag {
          margin-top: 2rem;
          display: inline-flex;

          .wp-text {
            font-size: 18px;
            font-weight: 500;
            word-spacing: 2px;
            text-align: center;
            letter-spacing: 5px;
            align-self: flex-start;
          }
        }
      }

      // end here
      .wp-service-offer {
        margin-top: 3rem;
        align-self: flex-start;

        .wp-service {
          font-weight: normal;
          letter-spacing: 2px;
          font-size: 25px !important;
        }

        .wp-sub-wrap {
          gap: 1vw;
          width: 100%;
          margin: auto;
          margin-top: 3rem;
          align-items: center;
          display: inline-grid;
          grid-auto-flow: column;
          justify-content: center;

          .wp-list-serivce {
            display: inline-grid;
            width: min(500px, 350px);

            .wp-text-parag {
              gap: 20px;
              text-align: center;
              display: inline-flex;

              .wp-text {
                font-size: 20px;
                margin-top: 2px;
                text-transform: uppercase;
              }

              .wp-arrow {
                font-size: 20px;
                font-weight: 900;
                color: var(--5DFF00);
                transform: translateY(15px);
                text-shadow: -6px 0.8px 10px var(--003CFF);
                animation: Rotate_arrow 3s linear infinite !important;
              }

              .wp-arrow-2 {
                width: 15px;
                height: 15px;
                margin-top: 11px;
                margin-left: 2px;
                position: absolute;
                border-radius: 50%;
                border: 1px dashed var(--FFE600);
                box-shadow: 0 0 15px var(--DDE6ED80);
              }
            }
          }

          .wp-img-slider {
            width: 100%;
            max-width: 850px;
            aspect-ratio: 1.8/1 !important;

            .wp-img-wrap {
              width: 100%;
              overflow: hidden;
              max-width: 850px;
              border-radius: 25px;
              aspect-ratio: 1.8/1 !important;
              border: 10px double var(--7C7C7C);
              box-shadow: 0 0 35px var(--6B5F5F);

              img {
                width: 840px;
                object-fit: cover;
                aspect-ratio: 5/3;
                object-position: center;
                transition: 10s ease-out;
              }
            }
          }
        }
      }
    }

    .wp-home-mvg {
      margin-top: 35px;
      display: inline-block;

      .wp-wrap-mvg {
        display: inline-flex;

        .wp-wrap-sub {
          width: 50%;

          .wp-item-subject {
            font-size: 25px;
            letter-spacing: 3px;
            font-family: fnt.$Lora-family;
          }

          .wp-item-content {
            width: 90%;
            font-size: 18px;
            text-indent: 2rem;
            letter-spacing: 3px;
          }
        }

        .wp-list-msvg {
          display: inline-grid;
          grid-auto-flow: row;

          .wp-list-items {
            gap: 25px;
            width: 80%;
            margin: 25px auto;
            align-items: center;
            flex-direction: row;
            display: inline-flex;
            justify-content: center;

            &:nth-child(2) {
              &::before,
              &::after {
                content: "";
                width: 1px;
                z-index: -1;
                height: 45px;
                position: absolute;
                background-color: var(--00A8CC);
                border: 1px solid var(--00A8CC);
                box-shadow: 0 0 15px var(--79E0EE);
                transform: matrix(0, 2, -4.7, 2.1, 0, -205);
              }

              &::after {
                transform: matrix(0, 2, 4.7, 2.1, 0, 205);
              }

              .wp-wrap-sub {
                order: 2;

                .wp-item-subject {
                  place-self: flex-start;
                  margin-left: 65px;
                }
                .wp-item-content {
                  place-self: flex-end;
                }
              }

              img {
                order: 1;
              }
            }

            .wp-wrap-sub {
              gap: 15px;
              grid-auto-flow: row;
              display: inline-grid;
            }

            img {
              width: 100%;
              max-width: 500px;
              aspect-ratio: 1/0.7;
              border-radius: 25px;
              border: 2px solid var(--00A8CC);
              box-shadow: 0 0 15px var(--79E0EE);
            }
          }
        }
      }
    }
    .wp-map-location {
      z-index: 1;
      margin-top: 50px;
      margin-bottom: 30px;

      .wp-location-pin {
        font-size: 25px;
        margin-left: 2.5rem;
      }

      .wp-wrap-map {
        width: 100%;
        margin: auto;
        height: 550px;
        margin-top: 5px;
        overflow: hidden;
        border-radius: 35px;
        border: 10px double var(--1A59758A);

        .wp-map-wrap {
          .wp-pin-location {
            width: 100%;
            height: 100%;
            aspect-ratio: 1/1;
          }
        }
      }
    }
  }
}
