@use "../../assets/scss/global/fonts" as fnt;
@use "../../assets/scss/global/variables" as var;
@use "../../assets/scss/global/default" as dft;

$width-add: 20px;
$font-add-5: 5px;
$font-add-10: 10px;
$padding-add: 15px;

.landing-page {
  background: var(--url-landing-background);
  background-position: 0 -175px;
  background-color: var(--11CBD7);
  background-repeat: no-repeat;
  background-size: cover;
  transition: ease 2s;
  height: 100%;

  &::before {
    content: "";
    width: 100%;
    height: 100%;
    display: block;
    position: fixed;
    background: var(--00000050);
  }

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    background-size: cover;
    z-index: -1;
    opacity: 0;
    background: var(--url-landing-img-src);
  }

  #wp-landing {
    width: 100%;
    margin: auto;
    text-align: center;
    align-items: center;
    display: inline-flex;
    justify-content: center;

    .wp-main-wrap {
      transform: translateY(215px);

      .wp-greating-wrap {
        display: block;

        .wp-welcome {
          background: linear-gradient(90deg, var(--FFE600), var(--7F762E), var(--368164), var(--693232), var(--972C2C), var(--452727));
          font-family: fnt.$Fuggles-family;
          -webkit-background-clip: text;
          color: var(--00000000);
          background-clip: text;
          display: inline-block;
          letter-spacing: 7px;
          word-spacing: 15px;
          font-size: 155px;
          font-weight: 900;
        }

        .wp-welcome-shadow {
          font-family: fnt.$Fuggles-family;
          letter-spacing: 7px;
          word-spacing: 15px;
          font-size: 155px;
          font-weight: 900;
          color: var(--00000000);
          text-shadow: 0px 0px 0px var(--FF0000);
          animation: shadow 5s alternate infinite;
          justify-content: center;
          margin: -7px 0 0 -2px;
          display: inline-flex;
          align-items: center;
          position: absolute;
          width: 100%;
          z-index: -1;
          left: 0;
        }

        .wp-message-welcome {
          margin-top: 5px;
          font-size: 25px;
          word-spacing: 3px;
          letter-spacing: 2px;
          color: var(--FFE600);
          text-shadow: 0 0 3px var(--0F142C);
          font-family: fnt.$Croissant-family;
        }

        .wp-message-select,
        .wp-message-greating {
          font-size: 20px;
          margin-top: 70px;
          font-weight: 700;
          margin-bottom: 40px;
          color: var(--E4CD00);
          font-family: fnt.$Lora-family;
          text-shadow: 0 0 3px var(--0F142C);
        }
      }

      .wp-button-wrap {
        display: inline-flex;
        margin-bottom: 35px;
        z-index: 1;
        gap: 45px;

        .wp-btn-landing {
          animation: boxshadow 5s alternate infinite;

          &:nth-child(1) {
            a {
              width: (dft.$default-weight + $width-add);
            }
          }

          a {
            gap: 5px;
            align-items: center;
            display: inline-grid;
            grid-auto-flow: column;
            justify-content: center;
            transition: background-size 0.3s ease-out;
            width: (dft.$default-weight + $width-add);
            background: var(--url-landing-button-background);
            background-size: 270px 150px;
            background-position: center;
            background-repeat: no-repeat;

            svg {
              opacity: 0;
              margin-right: -30px;
              transition: 0.7s ease-out;
              font-size: dft.$default-font-size-svg + $font-add-5;
            }

            &:hover {
              color: red;
              background-size: 280px 70px;

              & svg {
                opacity: 1;
                margin-right: 2px;
              }
            }
          }
        }
      }

      /* ends here */
      .active-class {
        opacity: 0;
        cursor: none;
        pointer-events: none;
        transition: opacity 0.6s;
      }
    }
  }
}
