/* fonts-sizes */
$font-45-20: clamp(45px, 2vw, 20px);
$font-35-20: clamp(35px, 2vw, 20px);
$font-25-19: clamp(25px, 2vw, 19px);
$font-20-15: clamp(20px, 2vw, 15px);
$font-19-16: clamp(19px, 2vw, 16px);
$font-18-14: clamp(18px, 2vw, 14px);
$font-17-13: clamp(17px, 2vw, 13px);
$font-16-12: clamp(16px, 2vw, 12px);
$font-15-11: clamp(15px, 2vw, 11px);
$font-14-10: clamp(14px, 2vw, 10px);
$font-13-09: clamp(13px, 2vw, 9px);
$font-12-08: clamp(11px, 2vw, 8px);
$font-11-07: clamp(12px, 2vw, 7px);

/* font weight */
$weight-heavy: 900;
$weight-x-bold: 800;
$weight-bold: 700;
$weight-x-medium: 600;
$weight-medium: 500;
$weight-normal: 400;
$weight-light: 300;
$weight-x-light: 200;
$weight-thin: 100;

/* transition */
$ease-007s: ease 0.7s;
$ease-006s: ease 0.6s;
$ease-005s: ease 0.5s;
$ease-004s: ease 0.4s;
$ease-003s: ease 0.3s;
$ease-002s: ease 0.2s;

$ease-in-out-007s: ease 0.7s;
$ease-in-out-006s: ease 0.6s;
$ease-in-out-005s: ease 0.5s;
$ease-in-out-004s: ease 0.4s;
$ease-in-out-003s: ease 0.3s;
$ease-in-out-002s: ease 0.2s;
