@use "./fonts" as fnt;

$default-height: 60px;
$default-weight: 200px;
$default-ul-ol-gap: 10px;
$default-font-weight: 400;
$default-font-size-a: 15px;
$default-line-height: 1.75;
$default-font-size-svg: 20px;
$default-letter-spacing: 1.75px;

.saijero-page-landing {
  // site remove default
  cursor: var(--url-cursor-pointer-blue), auto;
  scroll-behavior: smooth;
  background-size: cover;
  height: 100%;

  &::-webkit-scrollbar {
    display: none;
    overflow: hidden;
    scrollbar-width: none;
  }
  .site-container {
    overflow: visible;

    .site-header,
    .site-footer {
      display: none !important;
      pointer-events: none;
    }
  }

  .site-inner {
    padding: 0;
    width: 100%;
    margin: auto;
    display: initial;
    max-width: 1920px;
  }

  //   default added
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    // outline: 1px red solid;
  }

  p {
    line-height: $default-line-height;
  }

  img {
    width: 100%;
    max-width: 1200px;
    vertical-align: none !important;
  }

  a,
  ul,
  ol,
  li {
    list-style: none;
    text-decoration: none;
  }

  ol,
  ul {
    margin: auto;
    display: flex;
    gap: $default-ul-ol-gap;
  }

  small {
    font-size: 18px;
    color: var(--FFFFFF);
    font-family: fnt.$Poppins-family;
  }

  a,
  p,
  h1,
  h2,
  h3,
  h4,
  h5 {
    color: var(--FFFFFF);
    font-weight: $default-weight;
    font-family: fnt.$Poppins-family;
    letter-spacing: $default-letter-spacing;
  }

  button,
  a {
    border-radius: 8px;
  }

  a {
    cursor: var(--url-cursor-pointer-blue-select), auto;
  }
  button {
    cursor: var(--url-cursor-pointer-blue-select), auto !important;
  }

  button,
  input,
  optgroup,
  select,
  textarea {
    line-height: $default-line-height;
  }

  button,
  input[type="button"],
  input[type="reset"],
  input[type="submit"],
  .button {
    text-transform: uppercase;
    background-color: var(--FFFFFF);
    border: none;
    cursor: pointer;
    font-size: none;
    font-weight: none;
    padding: none;
    text-align: none;
    text-decoration: none;
    white-space: none;
    width: none;
    color: none;
  }

  button {
    border: none;
    outline: none;
    margin: auto 5px;
    text-align: center;
    align-items: center;
    display: inline-flex;
    justify-content: center;
    box-shadow: 0px 10px 20px var(--160708);

    svg {
      aspect-ratio: 1/1;
      color: var(--D11D1D);
      font-size: $default-font-size-svg;
    }

    a {
      color: var(--E4DF04);
      width: $default-weight;
      height: $default-height;
      background: var(--120101);
      font-size: $default-font-size-a;
    }
  }

  .wp-child-wrap {
    position: absolute;
    width: 50px;
    height: 50px;
    background: var(--url-cursor-pointer-blue);
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 1;
    transition: transform 0.5s;
    transform: scale(0.7);
    pointer-events: none;
    box-shadow: 0 0 0 #000;
  }

  .wp-toggle-page {
    .wp-main-wrap {
      width: 100%;
      margin: 0 auto;
      max-width: 1600px;
    }
  }

  h2,
  .h2,
  .archive-description .entry-title,
  .archive-title,
  .author-box-title {
    font-size: none;
    line-height: none;
  }
}
